<template>
  <div class="number-range">
    <div class="number-range-between">
      <p>Between :</p>
      <input type="number" v-model="dataRowItemOption.between" @change="setDataRowItemOption" class="ds-input" />
    </div>
    <div class="number-range-and">
      <p>And :</p>
      <input type="number" v-model="dataRowItemOption.and" @change="setDataRowItemOption" class="ds-input" />
    </div>
  </div>
</template>
<script>
import { defineComponent, ref, computed } from 'vue';
import { useStore } from 'vuex';

export default defineComponent({
  props: {
    index: Number,
    dataRow: Object
  },
  setup(props) {
    const store = useStore();
    // default value
    let dataRowItemOption = props.dataRow ? computed(() => props.dataRow.item.itemOption) : ref({
      between: 1,
      and: 10
    });

    store.commit("setDataRowItemOption", {
      index: props.index,
      dataRowItemOption: dataRowItemOption.value
    });

    // 선택한 data type을 store에 저장
    const setDataRowItemOption = (() => {
      store.commit("setDataRowItemOption", {
        index: props.index,
        dataRowItemOption: dataRowItemOption.value
      });
      console.log("dataRowItemOption -->", store.getters.getDataRowItemOption(props.index));
    });

    return {
      dataRowItemOption,
      setDataRowItemOption
    }
  },
});
</script>

